    width: 100%;
    height: 100vh;
    z-index: 10;
    padding-bottom: 130px;
    overflow: auto;
    opacity: 0;
    border: 0;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #fff;
    text-align: center;
    box-shadow: inset 0 0 10px rgba(102, 102, 102, 0.5);
  }
}

.header__nav.is-active {
  opacity: 1;
