  position: relative;
}

.header__nav-list li::after, .header__nav-list li::before {
  pointer-events: none;
  content: '';
  position: absolute;
  top: calc(100% + 13px);
  left: calc(50% - 4px);
  width: 8px;
  height: 8px;
  background-color: #e24b2e;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s ease;
}

