.chart {
   position: relative;
   box-shadow: 0 1px 35px 1px rgba(23, 23, 23, 0.42);
   border-radius: 10px;
   overflow: hidden;
   background-color: rgba(#383838, .8);
   &__nav {
      position: relative;
      padding: 0 70px;
      margin-bottom: 43px;
      height: 65px;
      background-color: #3e3e3e;
      &-list {
         display: flex;
         height: 100%;
         color: #fff;
         li {
            @include flexCenter;
            flex: 1;
            transition: .3s ease-in-out;
            cursor: pointer;
            &.slick-active {
               background-color: #552cf5; } } }
      &-btn {
         display: flex;
         flex-direction: column;
         align-items: center;
         color: #fff;
         &-title {
            margin-bottom: 5px;
            @include f('dp500', 20, 20);
            text-transform: uppercase; }
         &-descr {
            @include f('dp400', 13, 13); } }

      &-arr {
         position: absolute;
         top: calc(50% - 13px);
         z-index: 1;
         height: 26px;
         &:hover svg {
            fill: #552cf5; }
         &_prev {
            left: 20px;
            svg {
               transform: rotate(90deg); } }
         &_next {
            right: 20px;
            svg {
               transform: rotate(-90deg); } }

         svg {
            fill: $white;
            font-size: 15px;
            transition: .3s ease-in-out; }

         &.slick-disabled {
            pointer-events: none;
            svg {
               opacity: .5; } } } }
   &__tabs {
      padding: 0 45px 60px;
      &-item {
         height: 400px;
         @include gpu; } }
   &__label {
      position: absolute;
      z-index: 1;
      text-transform: uppercase;
      @include f('dp500', 13);
      color: #d7d7d7;
      span {
         color: #e24b2e; }
      &_x {
         left: 50%;
         bottom: 20px;
         transform: translateX(-50%); }
      &_y {
         top: 45%;
         left: 28px;
         transform: translateX(-50%) rotate(-90deg); } } }
