.footer {
   position: relative;
   padding: 162px 0 94px;
   background-color: #3727a3;
   color: #fff;
   @include f('dp400', 16);
   line-height: 1.2;
   overflow: hidden;
   &__container {
      position: relative;
      z-index: 1; }
   &__row {
      justify-content: space-between; }
   &__col {
      padding: 0 15px;
      &:nth-child(1) {
         flex: 0 0 270px; }
      &:nth-child(2) {
         flex: 0 0 540px;
         margin-left: -30px; }
      &:nth-child(3) {
         flex: 0 0 315px;
         margin-right: 15px; } }

   &__title {
      margin-bottom: 30px;
      @include f('dp500');
      font-size: 20px;
      text-transform: uppercase; }
   &__descr {
      &:not(:last-child) {
         margin-bottom: 15px; } }
   &__contacts {
      &-tel {
         display: inline-block;
         margin-bottom: 3px;
         text-transform: uppercase;
         font-size: 30px; }
      &-mail {}
      &-address {
         margin-top: 20px;
         font-style: normal; } }
   &__dev-link {
      display: block; }
   &__decor-rect {
      position: absolute;
      top: -152px;
      left: -65px;
      min-width: 1600px;
      z-index: -1; }
   &__socials {
      margin-bottom: 20px; } }

