.header {
   $parent: &;
   position: absolute;
   top: 0;
   left: 0;
   z-index: 100;
   width: 100%;
   padding: 60px 0;
   &__container {
      display: flex;
      justify-content: space-between;
      align-items: center; }
   &__logo {
      position: relative;
      &::before {
         position: absolute;
         top: -4px;
         right: 33px;
         width: 7px;
         height: 7px;
         background-color: #e24b2e;
         border-radius: 50%;
         content: ''; }
      &-label {
         position: absolute;
         top: -25px;
         transform: scaleY(-1);
         right: 16px;
         writing-mode: vertical-lr;
         @include f('dp400', 16, 16);
         color: #fff;
         text-transform: uppercase; }
      &-title {
         @include hide-text; } }
   &__btn {
      flex: 0 0 175px;
      width: 175px; }
   &__nav {
      @include xs {
         flex-direction: column;
         justify-content: center;
         position: absolute;
         top: 130px;
         left: 0;
         width: 100%;
         height: 100vh;
         z-index: 10;
         padding-bottom: 130px;
         overflow: auto;
         opacity: 0;
         border: 0;
         visibility: hidden;
         transform: translate3d(0, 100px, 0);
         transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
         background: #fff;
         text-align: center;
         box-shadow: inset 0 0 10px rgba($color1, .5); }
      &.is-active {
         opacity: 1;
         visibility: visible;
         transform: translate3d(0, 0, 0);
         transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease; }
      &-list {
         display: flex;
         justify-content: space-between;
         @include xs {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            overflow: auto;
            height: 100%;
            padding-bottom: 0; }
         li {
            position: relative;
            &::after, &::before {
               pointer-events: none;
               content: '';
               position: absolute;
               top: calc(100% + 13px);
               left: calc(50% - 4px);
               width: 8px;
               height: 8px;
               background-color: #e24b2e;
               border-radius: 50%;
               opacity: 0;
               transition: all 0.3s ease; }
            &:hover {
               &::before {
                  opacity: 1;
                  left: calc(50% - 20px); }
               &::after {
                  opacity: 1;
                  left: calc(50% + 12px); } }
            &:not(:last-child) {
               margin-right: 45px;
               @include xs {
                  margin-bottom: 30px; } }
            a {
               position: relative;
               display: block;
               font-size: 15px;
               line-height: 15px;
               text-transform: uppercase;
               color: #fff;
               transition: .3s ease-in-out;
               &::before {
                  pointer-events: none;
                  content: '';
                  position: absolute;
                  top: calc(100% + 13px);
                  left: calc(50% - 4px);
                  width: 8px;
                  height: 8px;
                  background-color: #e24b2e;
                  border-radius: 50%;
                  opacity: 0;
                  transition: all 0.3s ease; }
               @include sm {
                  font-size: 12px; }
               @include xs {
                  @include f('gp500');
                  font-size: 16px; }
               &:hover {
                  color: #e24b2e;
                  &::before {
                     opacity: 1; } } } } } }
   &__nav-btn {
      $parent: &;
      display: none;
      position: relative;
      top: -5px;
      border: 0;
      outline: 0;
      padding: 0;
      z-index: 11;
      @include xs {
         display: block; }

      &.is-active span {
         color: $color1; }

      &-burger {
         display: block;
         margin: auto;
         width: 40px;
         height: 40px;
         cursor: pointer; }
      &-lines {
         display: block;
         width: 40px;
         height: 3px;
         &:after, &:before {
            width: 40px;
            height: 3px; }
         position: relative;
         transform: translateY(25px);
         background: #fff;
         transition: all .3s;
         #{$parent}.is-active & {
            background: rgba(255, 255, 255, 0); }
         &:before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 10px;
            background: #fff;
            transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         &:after {
            content: "";
            position: absolute;
            left: 0;
            top: 10px;
            background: #fff;
            transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         #{$parent}.is-active & {
            &:after {
               top: 0;
               transform: rotate(45deg);
               transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
            &:before {
               bottom: 0;
               transform: rotate(-45deg);
               transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); } } } } }
