.form-field {
   $parent: &;
   &__input {
      width: 100%;
      height: 64px;
      padding: 0 22px;
      box-shadow: 0 1px 30px 1px rgba(4, 4, 4, 0.31);
      border: 1px solid #ffffff;
      border-radius: 10px;
      background-color: #3727a3;
      @include f('dp400', 23, 64);
      color: #fff;
      transition: .3s ease-in-out;
      @include placeholder-color(#fff);
      &:focus {
         background-color: #fff;
         color: #3727a3;
         @include placeholder-color(#3727a3); }
      #{$parent}_transparent & {
         background-color: transparent;
         text-align: center;
         color: #2b2b2b;
         border: 1px solid #3b1eaa;
         box-shadow: none;
         @include placeholder-color(#b1b1b1); } } }
.textarea {
   display: block;
   width: 100%;
   height: 242px;
   padding: 0 26px;
   resize: none;
   @include f('dp400', 23, 64);
   transition: .3s ease-in-out;
   box-shadow: 0 1px 30px 1px rgba(4, 4, 4, 0.31);
   border-radius: 10px;
   border: 1px solid #ffffff;
   background-color: #3727a3;
   color: #fff;
   @include placeholder-color(#fff);
   &:focus {
      background-color: #fff;
      color: #3727a3;
      @include placeholder-color(#3727a3); } }

// custom selects
.select2-container--default .select2-selection--multiple, .select2-container--default .select2-selection--single {
   position: relative;
   height: 50px;
   background-color: transparent;
   border-radius: 10px!important;
   border: 2px solid #ffffff; }
.select2-container--default .select2-selection--single .select2-selection__rendered {
   padding: 0 55px 0 13px;
   color: #fff;
   text-transform: uppercase;
   @include f('dp500', 30, 45); }
.select2-container--default .select2-selection--single .select2-selection__arrow {
   top: 0;
   right: 0;
   width: 55px;
   height: 50px;
   @include flexCenter; }
.select2-container--default .select2-results__option--highlighted[aria-selected] {
   background-color: #e24b2e; }
.select2-dropdown {
   background-color: #4838bd;
   border: 2px solid #ffffff;
   border-radius: 0 0 10px 10px!important;
   overflow: hidden;
   margin-top: -9px; }
.select2-container--classic.select2-container--open .select2-selection--single .select2-selection__arrow b, .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
   transform: scaleY(-1) translateY(5px); }

.custom-select-sm {
   + .select2-container {
      width: 222px!important; } }

// ranges
.js-range {
   .irs-max, .irs-min, .irs-single, .irs-bar-edge {
      display: none; }
   .irs-slider {
      height: 14px;
      width: 0;
      margin-left: -7px;
      top: auto;
      bottom: 8px;
      border: 0;
      box-shadow: none;
      background: 0; }
   .irs-line {
      height: 14px;
      border-radius: 7px;
      background-color: #e8e8e8;
      border: 0;
      cursor: pointer;
 }      //width: calc(100% - 14px)
   .irs-bar {
      left: 0 !important;
      height: 14px;
      border-radius: 7px;
      border: 0;
      background-image: linear-gradient(to right, #3b1eaa 0%, #3600ff 100%);
      transition: .3s ease-in-out;
      cursor: pointer; } }
